.webcam-capture {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;

    &>.btns {
        margin-bottom: 15px;

        &>* {
            padding: 10px 20px;
            margin: 0 10px;
            width: 100px;
            text-align: center;
            font-size: 16px;
        }
    }

    & .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
        width: 100%;
        margin-bottom: 20px;

        &>.camera {
            width: 350px;
            margin-bottom: 15px;
            height: 200px;
            position: relative;

            &>.demo {
                position: absolute;
                top: 0;
                left: -70px;
                z-index: 1;
            }
        }

        &>.container-screen-shot {
            position: relative;
            margin-top: 30px;
            width: 350px;
            height: 200px;
            &>.screen-shot {
                width: 100%;
                // margin: 20px 0 0 15px;
            }
            &>.demo {
                position: absolute;
                top: 0;
                left: -70px;
                z-index: 1;
            }
        }
    }
}